
import React from 'react';
import { NavLink } from 'react-router-dom';
import "./index.css";

const Navigation=()=> {
  return (
      <>
<div className="container-fluid nav_bg">
    <div className="row">
        <div className="col-10 mx-auto">

        

<nav className="navbar navbar-expand-lg navbar-light bg-light ">
  {/* <!-- Container wrapper --> */}
  <div className="container-fluid">
    <div className="navbar-brand">The MR.J</div>
    {/* <!-- Toggle button --> */}
    <button
      class="navbar-toggler"
      type="button"
      data-toggle="collapse"
      data-target="#navbarCenteredExample"
      aria-controls="navbarCenteredExample"
      aria-expanded="false"
      aria-label="Toggle navigation"
    >
      <span class="navbar-toggler-icon"></span>
    </button>

    {/* <!-- Collapsible wrapper --> */}
    <div
      className="collapse navbar-collapse"
      id="navbarCenteredExample"
    >
      {/* <!-- Left links --> */}
      <ul className="navbar-nav ml-auto mb-2 mb-lg-0">

 
        <li className="nav-item">
          <NavLink  exact activeClassName="manu_active" className="nav-link" to="/">Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName="manu_active"className="nav-link" to="/about">About</NavLink>
        </li>

        <li className="nav-item">
          <NavLink  exact activeClassName="manu_active"className="nav-link" to="/service">Service</NavLink>
        </li>

        <li className="nav-item">
          <NavLink exact activeClassName="manu_active"className="nav-link" to="/contact">Contact</NavLink>
        </li>

        
        {/* <!-- Navbar dropdown --> */}
       
       
      </ul>
      {/* <!-- Left links --> */}
    </div>
    {/* <!-- Collapsible wrapper --> */}
  </div>
  {/* <!-- Container wrapper --> */}
</nav>
</div>
    </div>

</div>










  </>);
}

export default Navigation;
